.Header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 7rem;
  background-color: var(--primary-500);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.HeaderLogo {
  width: 100%;
  display: block;
  object-fit: cover;
}

.HeaderImage {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.HeaderImage img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}
